.button {
  padding: 1rem 2rem;
  color: #060C14;
  font-weight: 500;
  background: white;
  border-radius: 0.8rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: 0;
}
.button:hover {
  background: #5F8CC7;
}
.button:disabled {
  cursor: default;
  opacity: 0.25;
}
.button:disabled:hover {
  background: white;
}
.button_alt {
  padding: 1rem 2rem;
  color: white;
  font-weight: 500;
  background: #1E334F;
  border-radius: 0.8rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: 2px solid #1E334F;
}
.button_alt:hover {
  background: #1E334F;
}
.button_alt:disabled {
  cursor: default;
  opacity: 0.25;
}
.button_alt:disabled:hover {
  background: #1E334F;
}
.button_alt:hover {
  border: 2px solid white;
}
.button_alt-active {
  padding: 1rem 2rem;
  color: white;
  font-weight: 500;
  background: #1E334F;
  border-radius: 0.8rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: 2px solid white;
}
.button_alt-active:hover {
  background: #1E334F;
}
.button_alt-active:disabled {
  cursor: default;
  opacity: 0.25;
}
.button_alt-active:disabled:hover {
  background: #1E334F;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  border-radius: 3rem;
}
.form__label {
  flex-grow: 1;
}
.form__input {
  background: transparent;
  color: white;
  border: 2px solid #1E334F;
  border-radius: 1.5rem;
  padding: 1rem;
  font-size: 1.4rem;
  width: 100%;
  box-sizing: border-box;
  outline-color: white;
}
.form__input:focus {
  outline-style: solid;
}
.form__input:invalid {
  border-color: #F18B7E;
  outline-color: #F18B7E;
}
.form__input::placeholder {
  color: #3e6aa4;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  height: max-content;
  position: absolute;
  display: none;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 100;
  padding-top: 5vh;
  padding-bottom: 5vh;
}
.modal_active {
  display: flex;
}
.modal__container {
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  max-width: 1320px;
  padding: 2.4rem 2.4rem 2.4rem;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  background-color: #0F2137;
  min-height: 90vmin;
  border-radius: 3rem;
  box-sizing: border-box;
  position: relative;
}
.modal__content {
  flex-grow: 1;
}
.modal__title {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 400;
}
.modal__actions {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
}
.modal__close {
  position: absolute;
  top: calc(2.4rem / 2);
  right: calc(2.4rem / 2);
  display: inline-block;
  width: 2rem;
  height: 2rem;
  font-size: 2rem;
  padding: 0;
  padding: 0;
  color: white;
  background: transparent;
  border: none;
  border-radius: 0;
  cursor: pointer;
  transition: color 0.3s ease;
}
.modal__close:hover {
  color: #5F8CC7;
}
.modal__close:disabled {
  cursor: default;
  opacity: 0.25;
}
.modal__close:disabled:hover {
  color: white;
}
.modal__footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
}
.modal__message {
  white-space: nowrap;
  max-width: 60%;
  text-overflow: ellipsis;
  overflow: hidden;
}
.modal__message_error {
  color: #F18B7E;
}

html, body.page {
  height: 100%;
  font-family: "YS Text", Roboto, Helvetica, Arial, sans-serif;
  font-size: 20px;
  color: white;
  background: #0F2137;
}

.page__wrapper {
  width: 100%;
}
.page__wrapper_locked {
  position: fixed;
}

.header {
  height: 9rem;
  background-color: #060C14;
}
.header__container {
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  max-width: 1320px;
  padding: 45px 2.4rem 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  height: 4.5rem;
}
.header__logo-image {
  display: block;
  height: 100%;
}
.header__basket {
  padding: 0;
  color: #060C14;
  background: transparent;
  border: none;
  border-radius: 0;
  cursor: pointer;
  transition: color 0.3s ease;
  background-image: url(../images/shopping_cart.svg);
  height: 58px;
  width: 58px;
  border: none;
  position: relative;
  cursor: pointer;
}
.header__basket:hover {
  color: #5F8CC7;
}
.header__basket:disabled {
  cursor: default;
  opacity: 0.25;
}
.header__basket:disabled:hover {
  color: #060C14;
}
.header__basket-counter {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: -0.2rem;
  width: 0.8rem;
  height: 0.8rem;
  font-size: 0.5rem;
  background-color: #83FA9D;
  color: #0F2137;
  border-radius: 100%;
  font-weight: 600;
}

.gallery {
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
  max-width: 1320px;
  padding: 2.4rem 2.4rem 2.4rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
  grid-gap: 24px;
}
.gallery__item {
  border: none;
  border-radius: 3rem;
  cursor: pointer;
  position: relative;
  transition: background-color 0.3s ease;
}
.gallery__item:hover {
  background-color: #1E334F;
}
.gallery__item:disabled {
  opacity: 0.25;
  cursor: default;
}
.gallery__item:hover:disabled {
  background-color: unset;
}
.gallery__item::after {
  border-radius: 0.8rem;
}

.card {
  display: flex;
  flex-direction: column;
  border: 0;
  border-radius: 3rem;
  background-color: #060C14;
  color: white;
  padding: 2.4rem;
}
.card_full {
  flex-direction: row;
  background-color: transparent;
}
.card_full .card__title {
  flex-grow: 0;
}
.card_compact {
  flex-direction: row;
  background-color: transparent;
  align-items: center;
  border-radius: 0;
}
.card_compact .card__title {
  flex-grow: 1;
}
.card__column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.card__row {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2.4rem;
}
.card__image {
  height: 390px;
  aspect-ratio: 1;
  margin: 3.75rem;
}
.card__title {
  font-size: 2.5em;
  font-weight: 500;
  margin: 0;
  flex-grow: 1;
}
.card__text {
  text-align: left;
  flex-grow: 1;
}
.card__category {
  padding: 0.5rem 1.25rem;
  border-radius: 2.5rem;
  margin-bottom: 12px;
  color: #060C14;
}
.card__category_soft {
  background-color: #83FA9D;
}
.card__category_hard {
  background-color: #FAA083;
}
.card__category_other {
  background-color: #FAD883;
}
.card__category_additional {
  background-color: #B783FA;
}
.card__category_button {
  background-color: #83DDFA;
}
.basket {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  height: 100%;
}
.basket__list {
  padding: 0;
  margin: 0;
  flex-grow: 1;
}
.basket__item {
  border-bottom: 1px solid #060C14;
  padding: 1.5rem 0;
}
.basket__item-index {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background-color: #060C14;
  color: white;
  margin-right: 1rem;
}
.basket__item-delete {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  font-size: 2rem;
  padding: 0;
  padding: 0;
  color: #5F8CC7;
  background: transparent;
  border: none;
  border-radius: 0;
  cursor: pointer;
  transition: color 0.3s ease;
  opacity: 0.5;
  margin-left: 4rem;
}
.basket__item-delete:hover {
  color: white;
}
.basket__item-delete:disabled {
  cursor: default;
  opacity: 0.25;
}
.basket__item-delete:disabled:hover {
  color: #5F8CC7;
}

.order {
  display: flex;
  flex-direction: column;
  gap: 4rem;
  margin-bottom: 4rem;
}
.order__field {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.order__buttons {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
}

.order-success {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 1rem;
}
.order-success::before {
  content: " ";
  background: url("../images/Subtract.png") no-repeat center;
  background-size: "cover";
  width: 256px;
  height: 256px;
}
.order-success__close {
  margin-top: 2rem;
}